body {
  font-family: Montserrat, sans-serif;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

h1, h2, h3, h4, h5, h6, p {
  margin-top: 0;
  margin-bottom: 0;
}

ul, ol {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
  list-style: none;
}

button.btn {
  color: #fff;
  cursor: pointer;
  height: 36px;
  background-color: #43b581;
  border: none;
  border-radius: 5px;
  margin-bottom: 60px;
  padding: 8px 16px;
  font-size: 16px;
  transition: background-color .3s;
}

button.btn:hover {
  background-color: #3fb072;
}

.cat-info {
  background: #f5f5f5;
  flex-wrap: wrap;
  gap: 60px 30px;
  margin-bottom: 60px;
  display: flex;
}

.cat-card {
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-start;
  gap: 20px;
  padding: 10px;
  display: flex;
  position: relative;
  box-shadow: 0 1px 6px rgba(46, 47, 66, .08), 0 1px 1px rgba(46, 47, 66, .16), 0 2px 1px rgba(46, 47, 66, .08);
}

img {
  width: 100%;
  max-width: unset;
  object-fit: cover;
  box-shadow: 0 1px 6px rgba(71, 71, 75, .49), 0 1px 1px rgba(46, 47, 66, .45), 0 2px 1px rgba(50, 50, 54, .26);
}

.info {
  flex-direction: column;
  justify-content: center;
  gap: 15px;
  display: flex;
}

.description {
  color: #232223;
  font-size: 16px;
  font-style: italic;
  font-weight: 500;
  display: block;
}

.is-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.loader {
  height: 32px;
  width: 200px;
  color: red;
  background: #fff;
  border: 2px solid #fff;
  margin: 0 auto;
  display: block;
  position: relative;
  overflow: hidden;
}

.loader:before {
  content: "";
  width: 0;
  height: 100%;
  background: red;
  animation: loading 10s linear infinite;
  position: absolute;
  top: 0;
  left: 0;
}

@keyframes loading {
  0% {
    width: 0;
  }

  100% {
    width: 100%;
  }
}

